import { Box } from '@mui/system';
import React, { useEffect, useRef } from 'react';

const Container = (props) => {
    useEffect(() => {
        const containerRect = containerElement.current.getBoundingClientRect();

        props.onContainerUpdate({
            ...props.container,
            style: {
                ...props.container.style,
                position: {
                    x: containerRect.x,
                    y: containerRect.y
                },
                size: {
                    width: containerRect.width,
                    height: containerRect.height
                }
            }
        })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const containerElement = useRef(null);

    const subContainers = props.container.containers.map((container) => (
        <Container
            key={container.id}
            container={container}
            onContainerUpdate={props.onContainerUpdate}
        />
    ))

    return <Box
        ref={containerElement}
        border="1px solid black"
        margin={props.container.style.margin[0] + 'px'}
        // height="100%"
        flexGrow="1"
        backgroundColor="lightgreen">
        <Box display="flex" height="100%">
            {subContainers}
        </Box>
    </Box>
};

export default Container;
