import React, { useState, Fragment } from 'react';
import { Button, Box, Typography } from '@mui/material';
import Drawer from '@mui/material/Drawer';


const ControlPanel = (props) => {
	const [open, setOpen] = useState(true);
	const [anchor, setAnchor] = useState('right');

	return (
		<div style={{
			position: 'fixed',
			top: '10px',
			right: '300px'
		}}>
			<Fragment>
				<Button onClick={() => setOpen(prevState => !prevState)}>Toggle</Button>
				<Drawer
					anchor={anchor}
					open={open}
					onClose={() => setOpen(false)}
					variant="persistent"
				>
					<Box width="200px" backgroundColor="rgba(121, 181, 219, 0.2)" height="100vh">
						<div>X: {props.cursorPosition.x}, Y: {props.cursorPosition.y}</div>
						<Button
							size="small"
							onClick={() => setAnchor(prevState => prevState === 'right' ? 'left' : 'right')}
						>
							Move to {anchor === 'right' ? 'Left' : 'Right'}
						</Button>
						<Button size="small" onClick={props.onModesToggle}>Switch to {props.inEditMode ? 'Lock' : 'Edit'}</Button>
						<Button size="small" onClick={() => props.onContainerAdd('row')}>Add horizontal container</Button>
						<Typography>Container (Horizontal / Vertical):</Typography>
						<Typography>Other flexbox properties displayed as icons similar like in Web flow Flexbox</Typography>
						{/* <span onClick={setRows}>+</span> */}
					</Box>
				</Drawer>
			</Fragment>
		</div >
	);
};

export default ControlPanel;
