import { Box } from '@mui/material';
import React from 'react';
import Container from '../Container/Container';

const Layout = (props) => {
    const containers = props.containers.map((container) => (
        <Container
            key={container.id}
            container={container}
            onContainerUpdate={props.onContainerUpdate}
        />
    ))

    return (
        <Box
            display="flex"
            flexDirection={props.dashboardContainer.direction}
            widht="100%"
            height="100%">
            {containers}
        </Box>
    )
};

export default Layout;
