import { useEffect, useState } from 'react';
import { v4 as uuidv4 } from 'uuid';
import safeJsonStringify from 'safe-json-stringify';
import lodashCloneDeep from 'lodash.clonedeep';
import ControlPanel from './components/ControlPanel/ControlPanel';
import Layout from './components/Layout/Layout';
import './App.css';

const { tableau } = window;

const SETTINGS_PREFIX = 'SETTINGS_PREFIX_';
const EXTENSION_NAME = 'Extension for tests';
const VERSION = '0.0.1';

console.log('Extension version: ' + VERSION);

const App = () => {
	const [dashboardContainer, setDashboardContainer] = useState(null);
	const [containers, setContainers] = useState([]);
	const [selectedContainer, setSelectedContainer] = useState(null);
	const [inEditMode, setInEditMode] = useState(true);
	const [cursorPosition, setCursorPosition] = useState({ x: 0, y: 0 });
	const [extensionInitialized, setExtensionInitialized] = useState(false);

	useEffect(() => {
		tableau.extensions.initializeAsync().then(() => {
			setExtensionInitialized(true);

			console.log('Extension context');
			console.log(tableau.extensions.environment.context);
			console.log('Extension mode');
			console.log(tableau.extensions.environment.mode);

			const dashboard = tableau.extensions.dashboardContent.dashboard;

			const extension = dashboard.objects.find(object => object.name === EXTENSION_NAME);
			moveAndResizeTableauObjects([			// resizing extension to full size of dashboard
				{
					dashboardObjectID: extension.id,
					x: 0,
					y: 0,
					width: Math.round(dashboard.size.width),
					height: Math.round(dashboard.size.height)
				}
			])


			// tableau.extensions.settings.erase(SETTINGS_PREFIX + dashboard.name);
			// tableau.extensions.settings.saveAsync();

			setAndSaveIntialData(dashboard);


			dashboard.addEventListener(tableau.TableauEventType.DashboardLayoutChanged, async () => {
				console.log('layout changed');
				// await timeout(3000).then(() => console.log('dashboard refreshed'))
			})
			dashboard.addEventListener(tableau.TableauEventType.WorkbookFormattingChanged, (sheets) => { })

			// const timeout = (ms) => {
			// 	return new Promise(resolve => setTimeout(resolve, ms));
			// }

			// return async () => {
			// 	console.log('refreshing dashboard')
			// 	await timeout(3000).then(() => console.log('dashboard refreshed'))
			// }
		})
	}, []);

	useEffect(() => {
		if (containers.length && containers.every(container => container.style.position)) {
			moveAndResizeTableauObjects(containers.filter(container => container.tableauObject && container.tableauObject.presentInDashboard)	// newly created empty containers won't have tableauObject in the begining	
				.map(container => (
					{
						dashboardObjectID: container.tableauObject.id,
						x: Math.round(container.style.position.x) + 5,
						y: Math.round(container.style.position.y) + 5,
						width: Math.round(container.style.size.width) - 10,
						height: Math.round(container.style.size.height) - 10
					}
				)))
		}
	}, [containers]);

	useEffect(() => {
		if (extensionInitialized) {
			tableau.extensions.setClickThroughAsync(!inEditMode);

			if (!inEditMode) {
				setTimeout(() => {
					tableau.extensions.setClickThroughAsync(false)
				}, 10000)
			}

		}
	}, [extensionInitialized, inEditMode]);

	const getParsedTableauObject = (tableauObject) => {
		return {
			id: tableauObject.id,
			name: tableauObject.name,
			type: tableauObject.type,
			position: {
				x: tableauObject.position.x,
				y: tableauObject.position.y
			},
			size: {
				width: tableauObject.size.width,
				height: tableauObject.size.height
			},
			isFloating: tableauObject.isFloating,
			isVisible: tableauObject.isVisible
		}
	}

	const getDefaultContaner = (tableauObject) => {
		return {
			id: uuidv4(),
			tableauObject: {
				...getParsedTableauObject(tableauObject),
				presentInDashboard: true
			},
			containers: [],
			style: {
				direction: 'row',
				padding: [10, 10, 10, 10],
				margin: [10, 10, 10, 10]
			}
		}
	}

	const getIntialData = (dashboard) => {
		const dashboardSettings = getSettingsData(dashboard.name);
		console.log('dashboard settings before save');
		console.log(dashboardSettings);
		const allSettings = tableau.extensions.settings.getAll();
		console.log('all settings');
		console.log(allSettings);

		const dashboardObjects = dashboard.objects.filter(tableauObject => tableauObject.name !== EXTENSION_NAME);
		let dashboardContainerToReturn = {
			size: {
				width: dashboard.size.width,
				height: dashboard.size.height
			}
		};
		let containersToReturn = [];

		if (dashboardSettings) {
			dashboardContainerToReturn = {
				...dashboardSettings.dashboardContainer,
				...dashboardContainerToReturn
			};
			let containersHaveTableauObjects = [];

			if (dashboard.objects.length) {
				containersHaveTableauObjects = dashboardObjects.map(tableauObject => {
					const containerSetting = dashboardSettings.containers
						.find(containerSetting => containerSetting.tableauObject && tableauObject.name === containerSetting.tableauObject.name);

					if (containerSetting) {					// if container setting exists in settings then use it as a container
						return {
							...containerSetting,
							tableauObject: {
								...containerSetting.tableauObject,
								presentInDashboard: true
							}
						};
					} else {								// otherwise create new, default container for object
						return getDefaultContaner(tableauObject);
					}
				});
			}

			// also handle subcontainers that are inside containers, with some recurring function?
			const containersInSettingsOnly = dashboardSettings.containers
				.filter(containerSetting => !containersHaveTableauObjects.some(container => container.id === containerSetting.id))
				.map(containerSetting => ({
					...containerSetting,
					tableauObject: {
						...containerSetting.tableauObject,
						presentInDashboard: false
					}
				}));

			containersToReturn = [...containersHaveTableauObjects, ...containersInSettingsOnly];
		} else {													// first time using extension on dashboard (there are no settings for dashboard)
			dashboardContainerToReturn = {
				...dashboardContainerToReturn,
				direction: 'row',
				padding: [10, 10, 10, 10]
			};

			if (dashboardObjects.length) {
				containersToReturn = dashboardObjects.map(tableauObject => getDefaultContaner(tableauObject));
			}
		}

		return {
			dashboardContainer: dashboardContainerToReturn,
			containers: containersToReturn
		}
	}

	const setAndSaveIntialData = (dashboard) => {
		const initialData = getIntialData(dashboard);

		if (tableau.extensions.environment.mode === 'viewing') {
			setContainers(initialData.containers);
			setDashboardContainer(initialData.dashboardContainer);

			// also handle subcontainers that are inside container, with some recurring function?
			const constainersToSave = initialData.containers.map(container => {
				const containerToReturn = lodashCloneDeep(container);			// getting rid of objects references so presentInDashboard property can be safety removed
				delete containerToReturn.tableauObject.presentInDashboard;		// presentInDashboard property is not needed in settings and can cause confusion and unexpected behavior
				return containerToReturn
			})

			const settingsToSave = safeJsonStringify({
				dashboardContainer: initialData.dashboardContainer,
				containers: constainersToSave
			})
			tableau.extensions.settings.set(SETTINGS_PREFIX + dashboard.name, settingsToSave);
			tableau.extensions.settings.saveAsync().then(result => {
				console.log('dashboard settings after save');
				console.log(getSettingsData(dashboard.name));
			}).catch(err => {
				console.dir(err);
			});
		}
	}

	const moveAndResizeTableauObjects = (resizeData) => {
		tableau.extensions.dashboardContent.dashboard.moveAndResizeDashboardObjectsAsync(resizeData);
	}

	const getSettingsData = (dashboardName) => {
		let dashboardData = tableau.extensions.settings.get(SETTINGS_PREFIX + dashboardName);
		if (dashboardData) {
			dashboardData = JSON.parse(dashboardData);
			return dashboardData;
		} else {
			return null;
		}
	}

	const updateContainer = (container) => {
		setContainers(prevContainers => {
			const containersToUpdate = [...prevContainers];
			let indexToUpdate = containersToUpdate.findIndex(containerToUpdate => containerToUpdate.id === container.id);
			containersToUpdate[indexToUpdate] = { ...container };
			return containersToUpdate;
		});
	}

	const toggleModes = () => {
		setInEditMode(prevState => !prevState);
	}

	const addEmptyContainer = (direction) => {// maybe this can be handled while droping tableau object on given side of the container - top / bottom or left / rigt
		if (selectedContainer) {		// add new container under other container

		} else {						// add new container on the dashboard level
			setDashboardContainer(prevState => ({
				...prevState,
				direction: direction === 'row' || 'row-reverse' ? 'column' : 'row'
			}));
			setContainers(prevContainers => {
				const updatedContainers = [
					{
						id: uuidv4(),
						tableauObject: null,
						containers: [...prevContainers],
						style: {
							direction,
							padding: [10, 10, 10, 10],
							margin: [10, 10, 10, 10]
						}
					},
					{
						id: uuidv4(),
						tableauObject: null,
						containers: [],
						style: {
							direction,
							padding: [10, 10, 10, 10],
							margin: [10, 10, 10, 10]
						}
					}
				];

				return updatedContainers;
			});
		}
	}

	const eraseAll = () => {
		const settings = tableau.extensions.settings.getAll();
		for (let key in settings) {
			tableau.extensions.settings.erase(key);
		}
	}

	return (
		dashboardContainer &&
		<div className="App" style={{ height: dashboardContainer.size.height + 'px', display: 'flex' }}>
			<ControlPanel
				inEditMode={inEditMode}
				cursorPosition={cursorPosition}
				onModesToggle={toggleModes}
				onContainerAdd={addEmptyContainer}
			// onComponentAdd={addComponent}
			/>
			{/* <div onMouseMove={event => setCursorPosition({ x: event.clientX, y: event.clientY })} style={{ */}
			<div style={{
				backgroundColor: 'transparent',
				width: dashboardContainer.size.width + 'px',
				height: '100%',
				borderRight: '1px solid black',
				// display: 'flex'
			}}>
				{/* <button onClick={setSetting}>Set setting</button>
				<button onClick={getSetting}>Get setting</button>
				<button onClick={getAllSettings}>Get all settings</button>
				<button onClick={saveSettings}>Save settings</button>
				<button onClick={eraseAll}>Erase all settings</button> */}
				<Layout
					dashboardContainer={dashboardContainer}
					containers={containers}
					onContainerUpdate={updateContainer}
				/>
				&nbsp;
			</div>
			{/* <div
				style={{
					position: 'fixed',
					top: '50px',
					left: '50px',
					width: '250px',
					height: '250px',
					backgroundColor: 'blue'
				}}
			>
				fsdgdfsfsdafdsfsdfs
			</div> */}
			{/* <div style={{ height: '100%', width: SIDE_PANEL_WIDHT + 'px' }}>
				<ControlPanel
					inEditMode={inEditMode}
					cursorPosition={cursorPosition}
					onModesToggle={toggleModes}
				// onComponentAdd={addComponent}
				/>
			</div> */}
		</div>
	);
}

export default App;
